import { Injectable, ɵCompiler_compileModuleAndAllComponentsAsync__POST_R3__ } from '@angular/core';
import { Log } from '../models/Log';
import { BehaviorSubject, Observable, of } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class LogService {
  
  logs: Log[];
  private logSource = new BehaviorSubject<Log>({id: null, text:null, date: null});
  selectedLog = this.logSource.asObservable();

  private stateSource = new BehaviorSubject<boolean>(true);
  clearState = this.stateSource.asObservable();

  constructor() {
   /*  this.logs = [
      { id: '1', text: 'Generated components', date: new Date('12/26/2018 12:54:12') },
      { id: '2', text: 'Added Bootstrap', date: new Date('12/27/2018 10:50:12') },
      { id: '3', text: 'Added log components', date: new Date('12/27/2018 11:54:13') }]; */
      this.logs = [];
  }

  getLogs(): Observable<Log[]> {
    if(localStorage.getItem('logs') === null){
      this.logs = [];
    } else {
      this.logs = JSON.parse(localStorage.getItem('logs'));
    }

    this.logs.sort((a,b) => {
      return new Date(b.date).getTime() - new Date(a.date).getTime();
    });

    return of(this.logs);
  }

  setLogForm(log: Log){
    this.logSource.next(log);
  }

  addLog(log : Log){
    this.logs.unshift(log);

    localStorage.setItem('logs',JSON.stringify(this.logs));
  }

  updateLog(log : Log){
    this.logs.forEach((cur,index)=>{
      if(cur.id === log.id){
        this.logs.splice(index,1);
      }
    });

  this.logs.unshift(log);

  localStorage.setItem('logs',JSON.stringify(this.logs));
  }

  deleteLog(log : Log){
    this.logs.forEach((cur,index)=>{
      if(cur.id === log.id){
        this.logs.splice(index,1);
      }
    });

    localStorage.setItem('logs',JSON.stringify(this.logs));
  }

  stateClear() {
    this.stateSource.next(true);
  }
}
